export default {
	data() {
		return {
			multipleSlots: true,// 在组件定义时的选项中启用多slot支持
			isHidden: true,
			cost:''
		};
	},
	props:{
		title: {            
		  type: String,    
		  default: '提示'    
		},
		btn_cancel: {
		  type: String,
		  default: '取消'
		},
		btn_certain: {
		  type: String,
		  default: '确定'
		},
		text:{
			type: String,
			default: '',
		}
	},
	watch:{
		
	},
	methods: {
		hide: function () {
		  this.isHidden=true;
		},
		show(e) {
		  this.isHidden=false;
			if(e!=null&&e!=undefined&&e!=""){
				this.text = e;
			}
		},
		/*
		 * 内部私有方法建议以下划线开头
 		 * triggerEvent 用于触发事件
		 */
		_cancel () {
			//触发cancel事件，即在外部，在组件上绑定cancel事件即可，bind:cancel，像绑定tap一样
		  this.cost = '';
		  this.hide();
		  this.$emit('onCancel');
		},
		_confirm () {
		  //this.triggerEvent("confirm");
		  console.log(this.cost);
		  this.$emit('onConfirm', this.cost);
		  this.cost = '';
		},
		_input(e){
			//将参数传出去，这样在getInput函数中可以通过e去获得必要的参数
			//this.triggerEvent("getInput",e.detail);
			this.cost = e.detail.value;
		}
	}
}
